$first-color: #0C0032;						
$second-color: #464866;		
$third-color: #211A36;	
$contact-form: #edf5e1;
$main-font: Georgia, 'Times New Roman', Times, serif;
$hover:#659DBD;
$hover-menu:#FF0700;
$text-color:white;

.nav {
    width: 200px;
    height: 100vh;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: $first-color;
  
    &__header {
    display:block;
    text-align: center;
    padding-bottom: 38px;
      a {
        height: 100%;
        width: 100%;
      }
      h3 {
        color: $text-color;
        padding: 25px 0 6px;
      }
      p { 
        color: $text-color;
      }
      &__dropbar {
        display: none;
      }
    }
  
    &__name__language {
      visibility: hidden;
      height: 0;
      height: 0;
    }
  
    &__header__logo {
      height:6rem;
      margin: 55.5px 0 8px;
    }
  
    &__menu {
      background-color: $second-color;
      display: block;
      text-align: center;
      height: 100%;
      padding: 10px 0;
      h3, p {
        visibility: hidden;
        height: 0;
      }
    }
  
    &__menu__item {
      padding: 15px 0;
      margin: auto;
      a {
        font-weight: bold;
        color: $text-color;
        font-size: 25px;  
        &:hover {
          color: $hover-menu;
        } 
      } 
    } 
  
    &__socialmedia  { 
      text-align: center;
      background-color: $first-color;
      margin-top: 15px;
      height: 100%;
      padding-top: 10px;
      a { 
        border-radius: 2px;
        color: $text-color;
        font-size: 30px; 
        margin: auto 10px;
        &:hover {
          transform:scale(1.1); 
          -webkit-transform:scale(1.1);
          color: $hover-menu;
        }
      }
    }
  }
  
  .nav__horizontal__line1, .nav__horizontal__line2 {
    visibility: hidden;
  }
  
  @media screen and (max-width: 700px) {
  
  .nav {
    width: 100%;
    height: 95px;
  
    &__header{
      margin: auto;
      padding-bottom: 0px;
      h3, p { 
      padding: 0px;
      visibility: hidden;
      }
    }
  
    &__dropbar {
      cursor: pointer;
      display: block;
      position: absolute;
      right:20%;
      top:32%;
      .nav__dropbar__line1, .nav__dropbar__line2, .nav__dropbar__line3 {
        display: block;
        width: 30px;
        height: 4px;
        margin: 6px auto;
        background-color: $text-color;
        transition: 0.4s;
      }
    }
  
    .change .nav__dropbar__line1 {
      -webkit-transform: rotate(-45deg) translate(-9px, 9px) ;
      transform: rotate(-45deg) translate(-8px, 6px) ;
    }  
     
    .change .nav__dropbar__line2 {
      opacity: 0;
    } 
  
    .change .nav__dropbar__line3 {
      -webkit-transform: rotate(45deg) translate(-9px, -9px) ;
      transform: rotate(45deg) translate(-8px, -6px) ;
    }
   
    &__name__language {
      visibility: visible;
      display: inline-block;
      position: absolute;
      right:80%;
      top:40%;
      font-size: 15px;
      color: $text-color;
      margin-right: -45px;
      li {
        cursor: pointer;
      }
      li ul {
        display: none;
      }
      li:hover ul {
        position: absolute;
        top:23px;
        right:-20px;
        display: block;
        background: $text-color;
        width: 90px;
        padding-top: 0px;
        z-index: 1;
        border-radius:5px;
      }
      li ul li {
        position: relative;
        text-align: left;
        padding: 5px;
        z-index: 2;
        font-size: 15px;
        margin-left: 20px;
        span {
          color: black;
          font-weight: bold;
          &:active {
            color:$hover-menu;
          }
        }
      }
    }
  
    &__triangle {
      position: absolute;
      top: 12px;
      right: -11px;
      z-index:10;
      height: 14px;
      overflow:hidden;
      width: 30px;
      background: transparent;
    }
  
    li:hover &__triangle:after {
      content: '';
      display: block;
      z-index: 20;
      width: 15px;
      transform: rotate(45deg) translateY(0px) translatex(10px);
      height: 15px;
      background: $text-color;
      border-radius:2px 0px 0px 0px;
      box-shadow: 0px 0px 20px rgba(0,0,0,0.2);
    }
  
    &__flag1 {
      width: 1.2rem;
      position: absolute;
      right:107%;
      top:2.3px;
    }
  
    &__flag2 {
      display: inline-block;
      width: 20px;
      height: 15px;
      position: absolute;
      top: 9px;
      right: 67px;
      background-image: url("/img/germanflag.svg");
    }
  
    &__header__logo {
      height:4rem;
      margin: 8px auto auto;
    }
  
    &__menu {
      position: fixed;
      left: -150%;
      top: 95px;
      z-index: 1;
      height: 100%;
      background-color: $second-color;
      width: 100%;
      text-align: center;
      transition: 0.5s;
      padding-top: 10px;
      h3, p {
        visibility: visible;
        color: $text-color;
        height: auto;
      }
      p {
        margin-bottom: -10px;
      } 
    }
  
      &__menu__item {
        padding: 2px 0;
        a {
          font-size: 20px;
          color: $text-color;
          &:hover {
            color: $hover-menu;           
          }
        }
      }
    } 
  
    .nav__horizontal__line1, .nav__horizontal__line2 {
      visibility: visible;
      display: inline-block;
      border: 2px solid $hover-menu;
      border-radius: 100%;
      width: 70%;
    }
  
    .nav__horizontal__line1 {
      margin-bottom:-2px;
    }
  
    .nav__horizontal__line2 {
      margin-bottom: 10px;
    }
  
    .nav__socialmedia  { 
      margin-top: -10px;
      padding-top: 0px;
      height: 0;
      a { 
        margin: auto 7px;
      }
    }
  
    .nav__menu.active {
      left: 0;
    }
  }