$first-color: #0C0032;						
$second-color: #464866;		
$third-color: #211A36;	
$contact-form: #edf5e1;
$main-font: Georgia, 'Times New Roman', Times, serif;
$hover:#659DBD;
$hover-menu:#FF0700;
$text-color:white;

.contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 30px 0 40px;
  &__form {
    width: 37%;
    font-size: 22px;
    display: block;
  }
}
  
input[type=text], textarea {
  display: flex;
  flex-direction: column;
  background-color: $hover;
  color: $text-color;
  width: 100%;
  padding: 12px;
  border-radius: 15px;
  margin: 6px 0 16px;
  font-size: 22px;
  font-family: $main-font;
  outline:none;
  resize: vertical;
  &:hover {
    border: 2px solid $hover-menu;
  }
}
  
input[type=text]:required:focus:invalid {
  border: 2px solid $hover-menu;
}
  
input[type=submit] {
  background-color: $hover-menu;
  color: $text-color;
  padding: 10px;
  border-radius: 15px;
  border: 2px solid $second-color;
  cursor: pointer;
  font-weight: bold;
  font-size: 20px;
  font-family: $main-font;
  width: 30%;
  position: relative;
  left: 35%;
  &:hover {
    background-color: lightcoral;
    filter: drop-shadow(1px 1px 5px   $hover-menu);
    &:active {
      color:lightskyblue;
    }
  }
}
  
form > label {
  font-weight: bold;
  color: $text-color;
}
  
.contact__vertical__line {
  border: 2px solid $text-color;	;
  height: 500px;
  margin: 80px 100px 0 100px;
  border-radius: 100%;
}
  
.contact__horizontal__line {
  display: none;
}
  
.contact__text {
  padding-top: 5px;
  display: grid;
  columns: 1;
  row-gap: 22px;
  text-align: center;
  font-size: 22px;
  p {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
  }

  &__phone p {
    padding-top: 10px;
  }
  
  &__email p {
    padding-bottom: 10px;
  }
    
  &__address {
    p:nth-child(2) {
      padding-top: 10px;
    }
  }
}
  
.contact__text__phone__icon1, .contact__text__email__icon2 {
  font-size: 80px;
  -webkit-transition: all 1s ease; /*reversed animation on mouse out events*/
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  -ms-transition: all 1s ease;
  transition: all 1s ease;
  color: $hover;
  &:hover {
    transform: scale(1.3);
    cursor: pointer;
  }
}
  
@media only screen and (max-width: 1100px) {
  
.contact {
  flex-direction: column;
  padding: 15px 0 50px;
  
  &__form {
  width: 70%;
  margin: auto;
  padding-bottom: 20px;
  font-size: 18px;
  }
}
  
input[type=text], input[type=submit], textarea {
  font-size: 18px;
}
    
.contact__vertical__line {
  display: none;
}
  
.contact__horizontal__line {
  display: inline-block;
  border: 2px solid white;
  border-radius: 100%;
  width: 70%;
  margin: 20px auto;
}
  
.contact__text {
  padding-top: 20px;
  row-gap: 30px;
  margin-bottom:-20px;
  p {
    font-size: 20px;
  }

  &__address {
    p:nth-child(2) {
      padding-top: 5px;
    } 
  }
}
}

@media only screen and (max-width: 900px) {
    .contact__form {
    width: 80%;

    }
  }
  
@media only screen and (max-width: 700px) {
  

  .contact__form {
    width: 90%;
    }
    
input[type=submit]:hover {
  background-color: $hover-menu;
  filter: none;
  transform: none;
}
  
.contact__text__phone__icon1, .contact__text__email__icon2 {
  font-size: 80px;
  -webkit-transition: none; 
  -moz-transition: none;
  -o-transition: none;
  -ms-transition: none;
  transition: none;
  &:hover {
    transform: none;
    cursor:auto;
  }
}
}





  


